<template>
    <div>
        <div>
            <div class="w-100 bg-light" style="margin-top:10px;">
                <b-list-group class="border-0">
                    <b-list-group-item class="border-0 bg-light">
                        <span>
                            <span style="font-size: 1.1em;font-weight:bold;">
                                 
                            </span>
                            <br>
                            <label for="card"><input checked type="radio" name="payMethod" id="card" value="" v-model="payMethod" v-on:click="setPaymethod"></label>
                            <label for="account"><input type="radio" name="payMethod" id="account" value="" v-model="payMethod" v-on:click="setPaymethod"></label>
                        </span>
                    </b-list-group-item>
                </b-list-group>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'OrderPayment',
        data () {
            return {
                payMethod: ''
            }
        },
        methods: {
            setPaymethod () {
                this.store.state.orderPayment = this.payMethod
                this.$forceUpdate();
                console.log(this.store.state.orderPayment);
            }
        }
    }
</script>

<style scoped>
    
</style>


